body {
  background-color: #1f1f1f;
}

a {
  color: white;
  text-decoration: none;
}
.Introduce {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 1%;
}

.Introduce-title {
    color: white;
}

.Introduce-club { 
    width: 650px;
    -webkit-justify-content: center;
            justify-content: center;
    color: rgb(134, 133, 133);
    display: -webkit-flex;
    display: flex;
}  

.Introduce-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 1%;
}

.Introduce-member {
    color: rgb(134, 133, 133);
    text-align: center;
    margin-right: 15rem;
}

.Introduce-history {
    color: rgb(134, 133, 133);
    text-align: center;
}

.Logo {
    height: 10rem;
    margin: 0 50px 0 0;
}

.Introduce-description {
    margin-left: 1%;
}

#Club-title {
    padding-top:1%;
}

@media screen and (max-width:767px){
    .Introduce-member {
        margin: 0;
    }

    .Introduce-club{
        width: 45%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }

    .Introduce-description {
        text-align: center;
    }

    .Introduce-wrapper {
        display: block;
    }

    .Logo {
        margin: 0;
    }
}
.Login {
    width: 290px;
    margin: 20vh auto;
    color: white;
}

.Login-box {
    display: table;
    height: auto;
    padding: 10px;
    font-size: 1rem;
    margin: 0 auto;
    width: 100%;
}

.Login-submit {
    display: table;
    margin: 0 auto;
    font-size: 1.5rem;
    border: 0;
    background-color: rgb(134, 133, 133);
    height: 40px;
    color: white;
    width: 110%;
}

.Regist-submit {
    display: table;
    margin: 10% auto;
    font-size: 1.5rem;
    border: 0;
    background-color: rgb(134, 133, 133);
    height: 40px;
    color: white;
    width: 110%;
}
.C {
    display: inline;
    margin-right: 8rem;
    
}

.Python {
    display: inline;
}

.Network {
    display: inline;
    margin-left: 8rem;
}

.Sub {
    text-align: center;
    margin-top: 13rem;
}

@media (max-width:767px) {
    .Sub {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        margin-top: 0;
        width: 100vw;
    }

    .C, .Network {
        margin: 0;
    }

    .C, .Python {
        margin-bottom: 5rem;
    }
}
.Submit {
    display: table;
    width: 300px;
    margin: 20vh auto;
    color: white;
}

.Submit-button {
    display: table;
    margin: 5% auto;
    font-size: 1.5rem;
    border: 0;
    background-color: rgb(134, 133, 133);
    height: 40px;
    color: white;
    width: 100%;
}

.Textbox {
    display: table;
    height: auto;
    padding: 10px;
    font-size: 1rem;
    margin: 0 auto;
    width: 90%;
}

.Text {
    padding: 10px;
    font-size: 1rem;
    margin: 0 auto;
    width: 92%;
    overflow: auto;
}

.Select {
    float: left;
    color: white;
}

.File {
    margin: auto;
    color: white;
}
.Category {
    width: 14rem;
    color: white;
    padding-left: 1rem;
    height: 35rem;
}

.Board {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 3rem;
}

.Board-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 5rem 0 5rem;
}

.Main {
    width: calc(100% - 15rem);
    padding: 0 1rem 0 1rem;
    border-left: 2px solid white;
}

.Title {
    color: white;
}

.Text {
    color: #b2b2b2;
}

.Create-Button, .Modify-button, .Delete-button {
    font-size: 1rem;
    border: 0;
    background-color: rgb(134, 133, 133);
    height: 2rem;
    color: white;
    width: 5rem;
    margin-right: 1rem;
}

.Buttons {
    margin-top: 1rem;
    text-align: right;
}

.Main .rdw-editor-toolbar {
    display: none !important;
}

.Content {
    color: #b2b2b2;
}
.editor-class {
    height: 30rem !important;
    background-color: white;
    border: 0.5rem solid gray;
}

.wrapper-editor {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 0;
    width: 100%;
}

.Save {
    display: table;
    margin: 0 auto;
    font-size: 1rem;
    border: 0;
    background-color: rgb(134, 133, 133);
    height: 2rem;
    color: white;
    width: 4rem;
    margin-top: 1rem;
    border-radius: 20%;
}

.rdw-editor-wrapper {
    width: 70rem;
    margin-top: 1rem;
}

.rdw-dropdown-selectedtext {
    color: black;
}

.Post-title {
    font-size: 1rem;
    margin-top: 2rem;
    padding: 10px;
    width: 68rem;
}

.rdw-editor-toolbar {
    background-color: #262626 !important;
}
.App-navbar {
    background-color: #262626;
    color: white;
    width: 100%;
}

.App-logo {
    background-color: #262626;
    margin: 0 0 0 1%;
    height: 4rem;
}

.menu ul li{
    display: inline;
    list-style-type: none;
    padding:0 0 0 20px;
}

.menu {
    float: right;
    margin-right: 4vh;
    margin-top: 0.5%;
}

.Menu-image {
    display: none;
}

#toggle {
    display: none;
}

@media (max-width: 767px){
    
    .Menu-image {
        display: block;
        float: right;
        margin: 1.2rem;
        width: 2rem;
    }

    #toggle {
        display: none;
    }

    #toggle:checked ~ .menu{display: inline;}

    .menu {
        background: #262626;
        position: absolute;
        right: 0;
        margin: 0;
        width: 100%;
        display: none;
        top: 4rem;
        border-top: 0.1rem solid white;
    }

    .menu ul li {
        display: block;
        text-align: center;
        margin: 1rem;
        padding: 0;
    }

    #nav {
        padding: 0;
    }

    li {
        padding: 0;
    }
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

